.App {
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
}
.App header .search-input {
  max-width: 15rem;
}
.App main {
  height: 100%;
}
.App main .artists-list-items {
  display: flex;
  justify-content: space-between;
}
.App main .accordion-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.App main .event-details {
  display: flex;
  justify-content: space-between;
}
.App main .event-card {
  height: 100%;
  padding: 2rem;
}
.App main .card-body * {
  margin: 1rem 0;
}
.App main .card-body * .person {
  border: 1px solid grey;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 60px;
  border-radius: 50%;
}
.App main .artist-card {
  height: 100%;
}
.App main .artist-card .artist-card-body {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.App main .artist-card .profile-pic {
  border: 1px solid grey;
  width: 80px;
  height: 80px;
  border-radius: 50%;
}
.App main .artist-card .artist-event-list {
  width: 100%;
}
.App main .container-auth {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}/*# sourceMappingURL=App.css.map */