
.App {
    text-align: center;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;

    header { 
        .search-input {
            max-width: 15rem;
        }
    }

    main {
        height: 100%;

        // Artists List
        .artists-list-items {
            display: flex;
            justify-content: space-between;
        }

        // Events Lists
        .accordion-header {
            width: 100%;
            display: flex;
            justify-content: space-between;
        }

        .event-details {
            display: flex;
            justify-content: space-between;
        }

        // Event 
        .event-card {
            height: 100%;
            padding: 2rem
        }

        .card-body * {
            margin: 1rem 0;

                .person {
                    border: 1px solid grey;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    height: 60px;
                    width: 60px;
                    border-radius: 50%;
                
            }
        }

        // Artist 
        .artist-card {
            height: 100%;

            .artist-card-body {
                display: flex;
                flex-direction: column;
                align-items: center;
            }

            .profile-pic {
                border: 1px solid grey;
                width: 80px;
                height: 80px;
                border-radius: 50%;
            }

            .artist-event-list {
                width: 100%;
            };
        }

        .container-auth {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }
}